<template>
  <div v-loading="loading">
    <h4>文档</h4>
    <div>
      <el-tag
        size="mini"
        v-for="(item, index) in category.doc.data"
        :key="index"
        style="margin-right:2px"
        >{{ item.name }}</el-tag
      >
    </div>
    <h4>题库</h4>
    <div>
      <el-tag
        size="mini"
        type="success"
        v-for="(item, index) in category.answer.data"
        :key="index"
        style="margin-right:2px"
        >{{ item.name }}</el-tag
      >
    </div>
    <h4>资源</h4>
    <div>
      <el-tag
        size="mini"
        type="warning"
        v-for="(item, index) in category.source.data"
        :key="index"
        style="margin-right:2px"
        >{{ item.name }}</el-tag
      >
    </div>
    <h4>图库</h4>
    <div>
      <el-tag
        size="mini"
        type="danger"
        v-for="(item, index) in category.picture.data"
        :key="index"
        style="margin-right:2px"
        >{{ item.name }}</el-tag
      >
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      // 分类
      category: {},
    };
  },
  methods: {
    // 请求分类
    getCategory() {
      this.$axios.get("getIndexCategory").then((re) => {
        this.category = re.data.data;
        this.loading = false;
        console.log(this.category);
      });
    },
  },

  created() {
    this.getCategory();
  },
};
</script>
