<template>
  <div>
    <!-- 顶部 start -->
    <header>
      <div class="top">
        <!-- logo -->
        <div class="logo">
          <img width="50px" height="50px" :src="website.web_logo" alt="" />
          <span>{{ website.web_title }}</span>
        </div>

        <!-- 导航栏
        //   <div class="top-nav">
        //     <ul>
        //       <li><a href="">首页</a></li>
        //       <li><a href="">首页</a></li>
        //       <li><a href="">首页</a></li>
        //       <li><a href="">首页</a></li>
        //     </ul>
        //   </div> -->
        <!-- 头像 -->
        <div class="login">
          <!-- 头像 <button>登录</button>-->
        </div>
      </div>
    </header>
    <!-- 顶部end -->

    <!-- 幻灯片部分 start -->
    <section>
      <div class="slide w">
        <div class="slide_left">
          <el-carousel height="400px">
            <el-carousel-item v-for="item in slide" :key="item">
              <el-image
                style="width: 100%; height: 400px"
                v-for="(item, index) in slide"
                :key="index"
                :src="item"
                fit="cover"
              ></el-image>
            </el-carousel-item>
          </el-carousel>
        </div>

        <div class="function">
          <category></category>
        </div>
      </div>
    </section>

    <!-- 幻灯片 end -->

    <!-- 主要部分 start -->
    <div class="main">
      <div>
        <div>
          <el-tabs class="tab w" v-model="activeName">
            <el-tab-pane label="资讯" name="first">
              <div class="detail">
                <!-- 列表 -->
                <ul>
                  <li
                    v-for="(item, index) in list"
                    :key="index"
                    @click="goArticle(item.id)"
                  >
                    <img :src="item.image_url" alt="" />
                    <h3>{{ item.title }}</h3>
                    <h4>
                      {{ item.content }}
                    </h4>

                    <div class="b">
                      <span class="author"> {{ item.author }}</span>
                      <span class="time"> {{ item.time }}</span>
                      <!--    <span class="label"> 新闻</span> -->
                      <span class="read"> {{ item.hits }}次</span>
                    </div>
                  </li>
                </ul>
              </div>
              <div class="page">
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="search.page"
                  :page-sizes="[10, 50, 100, 200]"
                  :page-size="search.eachPageNum"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="count"
                >
                </el-pagination>
              </div>
            </el-tab-pane>
            <el-tab-pane label="图库" name="second">
              <div class="block">
                <el-image
                  v-for="(item, index) in picList"
                  :key="index"
                  :src="item.file_path"
                  class="image"
                  :preview-src-list="[item.file_path]"
                ></el-image>
              </div>

              <div class="page">
                <el-pagination
                  @size-change="handleSizeChangePic"
                  @current-change="handleCurrentChangePic"
                  :current-page="search.page"
                  :page-sizes="[10, 50, 100, 200]"
                  :page-size="search.eachPageNum"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="count"
                >
                </el-pagination>
              </div>
            </el-tab-pane>
            <el-tab-pane label="文档" name="third">
              <el-descriptions
                v-for="(item, index) in docList"
                :key="index"
                :title="item.description"
                class="doc"
              >
                <el-descriptions-item label="用户名">{{
                  item.user
                }}</el-descriptions-item>
                <el-descriptions-item label="时间">{{
                  item.time
                }}</el-descriptions-item>
                <el-descriptions-item label="文件类型">{{
                  item.file_type
                }}</el-descriptions-item>
                <el-descriptions-item label="分类">
                  <el-tag size="small">{{ item.classify }}</el-tag>
                </el-descriptions-item>
                <el-descriptions-item label="标签">
                  <el-tag size="small">{{ item.user_defined_label }}</el-tag>
                </el-descriptions-item>

                <el-descriptions-item label="查看">
                  <el-button type="success" size="mini" @click="goDoc(item.id)"
                    >查看</el-button
                  >
                </el-descriptions-item>
              </el-descriptions>
              <div class="page">
                <el-pagination
                  @size-change="handleSizeChangeDoc"
                  @current-change="handleCurrentChangeDoc"
                  :current-page="search.page"
                  :page-sizes="[10, 50, 100, 200]"
                  :page-size="search.eachPageNum"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="count"
                >
                </el-pagination>
              </div>
            </el-tab-pane>
            <el-tab-pane label="题库" name="fourth">
              <div class="qu">
                <div class="bank_left">
                  <a-list>
                    <a-list-item v-for="(item, index) in bankList" :key="index">
                      <a-list-item-meta :description="item.time">
                        <a slot="title" href="#">{{ item.question }}</a>
                      </a-list-item-meta>
                      <div>{{ item.category }}</div>
                    </a-list-item>
                  </a-list>
                </div>
                <div class="bank_right">
                  <a-list>
                    <a-list-item
                      v-for="(item, index) in bankList1"
                      :key="index"
                    >
                      <a-list-item-meta :description="item.time">
                        <a slot="title" href="#">{{ item.question }}</a>
                      </a-list-item-meta>
                      <div>{{ item.category }}</div>
                    </a-list-item>
                  </a-list>
                </div>

                <div class="page1">
                  <el-pagination
                    @size-change="handleSizeChangeQu"
                    @current-change="handleCurrentChangeQu"
                    :current-page="search.page"
                    :page-sizes="[10, 50, 100, 200]"
                    :page-size="search.eachPageNum"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="100000000"
                  >
                  </el-pagination>
                </div>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>

        <!-- 搜索部分 -->
      </div>
      <!-- 渲染地方 -->

      <div>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
      </div>
    </div>
    <!-- 主要部分 end -->

    <!-- footer部分 start -->

    <footer>
      <div class="footer">
        <dl>
          <dt class="dh">友情链接</dt>
          <dt v-for="(item, index) in friend" :key="index">
            <a :href="item.url">{{ item.name }}</a>
          </dt>
        </dl>
        <div class="copyright">
          <span>{{ website.web_copyright }}</span>
          <br /><span>
            <a :href="website.web_beian_url">{{ website.web_beian }}</a></span
          >
        </div>
      </div>
    </footer>

    <!-- foot部分 end -->
  </div>
</template>

<script>
import category from "../components/category.vue";
export default {
  components: {
    category,
  },
  data() {
    return {
      search: {
        page: 1,
        eachPageNum: 10,
        search: "全部",
      },

      loading: false,
      //  题库列表
      bankList: [],
      bankList1: [],
      //   图片列表
      picList: [],
      //   图片列表
      docList: [],
      //   总数
      count: 0,
      activeName: "first",
      //   列表
      list: {},
      // 网站配置信息
      website: {},
      // 幻灯片
      slide: {},

      // 友情链接
      friend: {},
      //   版心导航
      banNav: [
        { name: "资讯", to: "news" },
        { name: "图库", to: "picture" },
        { name: "题库", to: "bank" },
        { name: "文档", to: "doc" },
        { name: "资源", to: "source" },
      ],
    };
  },
  methods: {
    //
    goDoc(id) {
      this.$router.push({
        path: `/doc/${id}`,
      });
    },
    // 跳转
    goArticle(id) {
      this.$router.push({
        path: `/article/${id}`,
      });
    },
    // 获取题库列表1
    getbankList() {
      this.$axios
        .get("getQu", {
          params: {
            page: this.search.page,
            eachPageNum: 20,
            search: "全部",
          },
        })
        .then((re) => {
          this.bankList = re.data.data;
          console.log(this.bankList);
        });
    },

    // 获取题库列表2
    getbankList1() {
      this.$axios
        .get("getQu", {
          params: {
            page: this.search.page + 1,
            eachPageNum: 14,
            search: "全部",
          },
        })
        .then((re) => {
          this.bankList1 = re.data.data;
          console.log(this.bankList);
        });
    },
    // 获取资讯列表
    getNewsList() {
      this.$axios
        .get("getNewsList", {
          params: this.search,
        })
        .then((re) => {
          this.count = re.data.count;
          this.list = re.data.data;
          console.log(this.list);
        });
    },

    // 获取图片列表
    getpicList() {
      this.$axios
        .get("getPictureIndexList", {
          params: this.search,
        })
        .then((re) => {
          this.count = re.data.count;
          this.picList = re.data.data;
          console.log(this.picList);
        });
    },
    // 文档
    getdocList() {
      this.$axios
        .get("getDocIndexList", {
          params: this.search,
        })
        .then((re) => {
          this.count = re.data.count;
          this.docList = re.data.data;
          console.log(this.picList);
        });
    },
    //请求网站配置信息
    getWebsite() {
      this.$axios.get("/getWebsiteInformation").then((re) => {
        this.website = re.data.data;
        console.log(this.website);
      });
    },
    // 请求友情链接
    getfriend() {
      this.$axios.get("getIndexFriendUrl").then((re) => {
        this.friend = re.data.data;
        console.log(this.friend);
      });
    },

    // 请求幻灯片
    getSlide() {
      this.$axios.get("getSlide").then((re) => {
        this.slide = re.data.data;
        console.log(this.slide);
      });
    },

    // *************文档***********
    // 分页
    handleCurrentChangeDoc(val) {
      this.search.page = val;
      this.getdocList();
    },
    // 每页数改变
    handleSizeChangeDoc(val) {
      this.search.eachPageNum = val;
      this.getdocList();
      console.log(`每页 ${val} 条`);
    },

    // *************图片***********
    // 分页
    handleCurrentChangePic(val) {
      this.search.page = val;
      this.getpicList();
    },
    // 每页数改变
    handleSizeChangePic(val) {
      this.search.eachPageNum = val;
      this.getpicList();
      console.log(`每页 ${val} 条`);
    },
    // *************资讯***********
    // 分页
    handleCurrentChange(val) {
      this.search.page = val;
      this.getNewsList();
    },
    // 每页数改变
    handleSizeChange(val) {
      this.search.eachPageNum = val;
      this.getNewsList();
      console.log(`每页 ${val} 条`);
    },
    //*********题库*****************
    // 分页
    handleCurrentChangeQu(val) {
      this.search.page = val;
      this.getbankList();
      this.getbankList1();
    },
    // 每页数改变
    handleSizeChangeQu(val) {
      this.search.eachPageNum = val;
      this.getbankList1();
      console.log(`每页 ${val} 条`);
    },
  },
  mounted() {
    this.getSlide();

    this.getWebsite();
    this.getfriend();
    this.getNewsList();
    this.getpicList();
    this.getdocList();
    this.getbankList();
    this.getbankList1();
  },
};
</script>

<style>
@font-face {
  font-family: "icomoon";
  src: url("../assets/fonts/icomoon.eot?a1t9a9");
  src: url("../assets/fonts/icomoon.eot?a1t9a9#iefix")
      format("embedded-opentype"),
    url("../assets/fonts/icomoon.ttf?a1t9a9") format("truetype"),
    url("../assets/fonts/icomoon.woff?a1t9a9") format("woff"),
    url("../assets/fonts/icomoon.svg?a1t9a9#icomoon") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

.w {
  width: calc(100% - 2 * 156px);
  margin: auto;
}

.top {
  height: 80px;
  border-bottom: 1px solid #d7dae0;
}

.tab .detail:after {
  /* 伪元素必须写 */
  content: "";
  /* 插入的元素为块级元素 */
  display: block;
  /* 不要看见元素 */
  height: 0;
  clear: both;
  /* 不要看见元素 */
  visibility: hidden;
}

.qu:after {
  /* 伪元素必须写 */
  content: "";
  /* 插入的元素为块级元素 */
  display: block;
  /* 不要看见元素 */
  height: 0;
  clear: both;
  /* 不要看见元素 */
  visibility: hidden;
}

.top .logo {
  float: left;
  padding-top: 20px;
  margin-left: 150px;
}

.top .logo span {
  font-size: 1.3rem;
  font-weight: 600;
  color: #2c3e50;
}

.top .login {
  float: right;
  height: 90px;

  line-height: 90px;
  margin-right: 200px;
}

.slide {
  height: 400px;
  margin-top: 5px;
}

.slide .slide_left {
  float: left;
  width: 65%;
  height: 100%;
}

.slide .function {
  float: left;
  margin-left: 3px;
  width: 34.6%;
  height: 100%;
  margin-top: -5px;
  border: 1px solid #d6d9df;
}

.function h4 {
  margin: 2px 3px;
}

.main .search {
  float: left;
}

.dh {
  display: inline-block;
  font-weight: bold;
  margin-bottom: 6px;
}

.footer {
  width: calc(100% - 2 * 160px);
  margin: auto;
  margin-top: 50px;
  padding-top: 14px;
  border-top: 1px solid black;
}

.footer dt {
  padding-left: 2em;
}

.footer a {
  text-decoration: none;
  color: black;
}

.footer a:hover {
  color: #396df8;
}

.copyright {
  padding-top: 10px;
  text-align: center;
}

.detail {
  margin-top: 20px;

  width: 100%;
}

.detail:after {
  clear: both;
}

.detail ul li {
  float: left;
  width: 200px;
  height: 300px;
  margin-right: 17px;
  margin-bottom: 25px;
  font-family: "icomoon";
  border-radius: 13px;
  box-shadow: 0px 2px 6px 0px rgba(0, 0, 0, 0.05);
  transition: transform 0.3px;
}

.detail ul li:hover {
  transform: translateY(-3px);
}

.detail ul li img {
  width: calc(100%);
  height: 60%;
}

.detail ul li h3 {
  font-size: 14px;
  margin: 10px 10px 10px 10px;
}

.detail ul li h4 {
  font-size: 4px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  /* 两行 */
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-weight: 400;
  margin: 10px 10px 10px 10px;
}

.tab {
  padding: 0;
}

.page {
  margin-top: 10px;
  text-align: center;
}

.page1 {
  float: left;
  margin-top: 10px;
  display: block;
  text-align: center;
}

.image {
  width: 230px;
  height: 230px;
  margin: 5px;
}

.doc {
  margin-top: 5px;

  border-bottom: 1px solid #1e1e1e;
}

.bank_left {
  float: left;
  width: 47%;
}

.bank_right {
  float: left;
  width: 47%;
  margin-left: 20px;
  padding-left: 20px;
}

.page1 {
  text-align: center;
  width: 100%;
  height: 90px;
}
</style>
